<template>
  <el-row class="mt-6">
    <el-calendar>
      <template #dateCell="{ data }" :v-model="defaulttime">
        <p :class="data.date.getDay()==0||data.date.getDay()==6  ? 'is-selected' : ''">
          {{ data.day.split('-').slice(1).join('-') }}
          {{getWorkTime(data)}}
        </p>
      </template>
    </el-calendar>
    <el-tag class="mx-4" size="large">上班第一天</el-tag>
    <div class="demo-date-picker">
      <el-date-picker
          v-model="value"
          type="date"
          placeholder="Pick a day"
          format="YYYY/MM/DD"
          value-format="YYYY-MM-DD"
          @change="getdate"
      >
        <template #default="cell">
          <div class="cell" :class="{ current: cell.isCurrent }">
            <span class="text">{{ cell.text }}</span>
          </div>
        </template>
      </el-date-picker>
    </div>
  </el-row>
</template>

<script >

import { ref } from 'vue'

const value = ref('2022-02-18')
const defaulttime='2022/02/18 00:00'
const day=5
const condition=2
const conditionless=3
let bb="白班"
let yb="夜班"
export default {
  data() {
    return {  value,defaulttime,day,condition,conditionless}
  },
  // computed: {
  //   // workTime(data){
  //   //  return  Math.floor(Math.floor(Math.abs(data.date- new Date(defaulttime))/ (24 * 3600 * 1000)) % 5) >=2 ? '' :  '✔️'
  //   // }
  //   workTime(data){
  //       return (defaulttime) => {
  //      return
  //   }
  // },
  methods: {
    getdate(e){

      this.defaulttime=e+" 00:00"
      console.log(this.defaulttime)
    },
    getWorkTime(data){

      const distance=data.date- new Date(this.defaulttime)
      const days= 24 * 3600 * 1000
      if(distance>0) {
        const num=Math.floor(distance/ (days) )
        let numday = Math.floor(num % this.day)
        if(numday==0){
          return bb
        }else if(numday==1){
          return yb
        }else{
          return ''
        }
        //console.log(numday,numday>=this.condition ? '' :  '✔️')
        //return  numday>=this.condition ? '' :  '✔️'
      }else{
        const num=Math.floor(Math.abs(distance)/ (days) )
        let numday = Math.floor(num % this.day)
        //console.log(numday,numday<this.conditionless ? '' :  '✔️')

        if(numday==4){
          return bb
        }else if(numday==3){
          return yb
        }else{
          return ''
        }
        //return numday<this.conditionless ? '' :  '✔️'

      }

    }
  }
}

</script>

<style scoped>
.is-selected {
color: #1989fa;
}
.el-tag {
  height: 38px;
  line-height: 34px;
}
</style>
